.jkit-notice-banner {
	position: relative;
	display: flex;
	margin: 10px 20px 20px 0px;
	padding: 0;
	border: 1px solid #c3c4c7;
	border-left-width: 0;

	.jkit-notice-banner-logo {
		background: #ecf0f8;
		padding: 20px 15px;
		border-left: 4px;
		border-right: 0;
		border-style: solid;
		border-image: linear-gradient(to bottom, #3f3bf7, #5cd0da) 1 100%;

		img {
			width: 24px;
			height: 24px;
		}
	}

	.jkit-notice-banner-content {
		padding: 20px 20px 25px 20px;
		width: 100%;

		h2 {
			margin-top: 0;
			margin-bottom: 10px;
		}

		.jkit-notice-action {
			display: flex;
			justify-content: space-between;
			margin-top: 20px;

			a {
				text-decoration: none;
			}

			.jkit-notice-action-button,
			.jkit-fix-action-button {
				color: #fff;
				background: #1b67a5;
				border-radius: 3px;
				cursor: pointer;
				padding: 7px 15px;
				text-decoration: none;
			}

			.jkit-notice-action-right {
				display: flex;
				align-items: center;
				gap: 5px;

				a {
					display: flex;
					gap: 7px;
					text-decoration: none;
					color: #1b67a5;
					justify-content: center;
					align-items: center;

					&:before {
						display: block;
						font-size: 16px;
					}
				}
			}

			.jkit-notice-action-left {
				display: flex;
				gap: 20px;
				vertical-align: middle;
				align-items: center;

				span {
					color: #ffb900;
				}

				.jkit-notice-close {
					display: flex;
					align-items: center;
					gap: 7px;

					svg * {
						fill: #2271b1;
					}

					&:hover {
						svg * {
							fill: #135e96;
						}
					}
				}
			}
		}
	}

	.jkit-banner-close {
		position: absolute;
		top: 20px;
		right: 20px;

		svg {
			width: 10px;
			height: 10px;
			cursor: pointer;
		}

		&:hover {
			svg * {
				fill: #fd3d31;
			}
		}
	}
}
